trí tuệ mới lạ để các con thoả sức trải nghiệm, khám phá và chinh phục những tấm huy chương vàng Toán Quốc tế.\n                    </p> <p data-v-426a12e2>\n                      Đồng hành cùng các con là tinh thần đầy nhiệt huyết, tận tâm của các cô giáo URI, với mong muốn cuộc thi không chỉ mang lại\n                      cho các con những kiến thức quan trọng về Toán học mà còn giúp các con luôn tự tin và vững vàng khi đối mặt với những thử\n                      thách, luôn năng động, tư duy trong các hoạt động học tập.\n                    </p></div></div></div> <div class=\"dsmall-item side-three\" data-v-426a12e2><div data-aos=\"fade-up-cus\" class=\"sidebar\" data-v-426a12e2><div class=\"side-fixed\" data-v-426a12e2><div class=\"side-fixed-wrap\" data-v-426a12e2><div id=\"m_category_list-2\" class=\"widget news-sidebar widget_m_category_list\" data-v-426a12e2><div class=\"sidebar-block\" data-v-426a12e2><div class=\"sidebar-head\" data-v-426a12e2><img width=\"15\" height=\"15\" src=\"data:image/svg+xml,%3Csvg%20xmlns='http://www.w3.org/2000/svg'%20viewBox='0%200%2015%2015'%3E%3C/svg%3E\" alt data-lazy-src=\"https://ischool.vn/template/assets/images/icon-dm.svg\" class=\"icon\" data-v-426a12e2><noscript data-v-426a12e2><img width=\"15\" height=\"15\" src=\"https://ischool.vn/template/assets/images/icon-dm.svg\" alt class=\"icon\" data-v-426a12e2></noscript> <p class=\"sidebar-title\" data-v-426a12e2>Danh mục</p></div> <div class=\"sidebar-body\" data-v-426a12e2><div class=\"sidebar-menu\" data-v-426a12e2><div class=\"sidebar-menu-list\" data-v-426a12e2></div></div></div></div></div></div> <div class=\"side-close\" data-v-426a12e2><i class=\"fas fa-times close icon\" data-v-426a12e2></i></div></div> <div class=\"side-open\" data-v-426a12e2><div class=\"side-open-wrap\" data-v-426a12e2><img width=\"15\" height=\"15\" src=\"data:image/svg+xml,%3Csvg%20xmlns='http://www.w3.org/2000/svg'%20viewBox='0%200%2015%2015'%3E%3C/svg%3E\" alt data-lazy-src=\"https://ischo